          text: '南寮村就以古厝群逐漸打響觀光知名度，來這欣賞古厝群，看看先民以咾咕石建屋的智慧遺跡，這也是鄉村內的私房景點。<br><br>社區裡的福記魚灶也是改造修復的重點之一，1959年由戴福記建造，整體分為灶台、煙囪、鹹汁槽三部份，2012年空間營造計畫補助修整，使魚灶風貌得以再現，讓遊客可以體驗澎湖傳統魚灶產業。<br><br>還有牛屎窟體驗區，2013年透過行政院農委會水保局農村再生培根計畫課程補助，經由村民共同規劃牛屎窟體驗區，重現早期農村生活原貌。早期農業生活牛屎餅是生活的重要燃料，村民將收集來的牛糞去除水分後並揉成餅狀，日曬風乾成牛屎餅，以作為家中的燃料。區內設置了牛糞坑及早年收集牛糞的相關工具，讓遊客了解早期農村文化外也能體驗製作牛屎餅的過程。<br><br>除了早期漁獲產業以外，南寮村是傳統的務農聚落，在2014年的空間營造計畫中，提倡「小農市集」，村民利用浮球裝置藝術與彩繪共同努力打造市集場所，提供村民以及附近的社區居民可販售自家栽種的新鮮蔬果與捕獲的海味，提倡無毒農作及減少食物碳足跡的觀念。',
