          text: '雁情嶼，古稱黿龍嶼，在退潮時嶼的西邊有狹長像背脊的岩脈，被稱之為龍尾，而嶼的東邊像是黿（鰲魚），因而命名為黿龍嶼。除此之外，還有雁淨嶼、雁崢嶼、黿龍嶼、顏靖嶼、目鏡嶼…等別名。<br><br>而現今雁情嶼此名有幾種傳說，相傳島上有對雁鴨，因公雁鴨外出覓食，導致獨留島上的母雁鴨被抓走，公雁鴨為了尋找母雁鴨長居此島，到生命結束都未離開。村民以此典故而命名，舊稱言情嶼，後改為雁情嶼。除此之外，還有另種說法：早期永安與中正橋尚未搭建時，白沙和中屯地區的村民常要涉水往來此路線將農漁產品送往馬公，而此島嶼就位於行經路線旁，近在眼前，因而命名為”眼前嶼”，口耳相傳後久之就將諧音翻成了雁情嶼。<br><br>雁情嶼面積0.0069平方公里、海岸線0.331公里，最高高度14公尺。地質大多屬玄武岩地形，在島嶼的北面以及南面有部分的紅色火山角礫岩及紅色的土壤層，紅色的岩層主要是因為岩石長期受海時氧化的結果。因地理位置靠近湖西鄉中西村，早期中西村的居民會牽牛到此地種植蕃藷。島上還有碉堡遺跡及深洞，據傳是日治時代日本士兵在此地深挖地道，以防美軍登陸；除此之外，三、四十年前也曾有國軍駐守此島。<br><br>退潮時的雁情嶼是座陸連島，可從中正橋下涉水途經四周顯露出來大面積的潮間帶，登陸此座無人島。不過必須注意潮汐時間，漲潮後潮間帶被海水包覆已不見來時路，海面上只露出了雁情嶼的玄武岩地質，美麗孤獨而神秘，別有一番風貌。',
