          text: '龍門鼓浪是澎湖古八景之一，指的是龍門靶場這片海域，因為暗礁密布，海底高低落差非常大，當海浪進入龍門附近的海域後，因為海蝕平台的地形阻擋關係，海浪與地形交會發出了像是打鼓般的轟隆聲。尤其是在冬季時的東北季風或是颱風前後，因面朝東方，強勁的風力正面吹襲而來更助長了水勢，正面襲擊岸邊，海濤怒吼，浪花飛濺，像是大自然氣候與地形環境的角力爭鬥，聲音與景色震撼人心，如鼓聲敲打在心上轟隆作響，這就是龍門鼓浪的奇狀景觀。澎湖進士蔡廷蘭所作「澎湖八景詩」中，即有「夜靜龍門聽鼓浪」的描述。<br><br>天氣好時可以遠眺查母嶼燈塔，位於裡正角東南約3.5公里處，是位於澎湖極東的小島。地質由緻密黝黑的玄武岩所構成，節裡均呈現倒臥狀，遠眺像似一個女人平躺於海面，胸腹線條分明。查母嶼上有座磚造八角形的燈塔，於西元1913年啟用，塔身外面是黑白鄉間的垂直條紋，塔高13.4公尺，燈篙24.7公尺，裝設太陽能發電的五等電石器閃光燈，每6秒閃光一次，光力為1200燭光，光程9浬，是澎湖群島東側海域往來船隻的重要指引。<br><br>在岸邊有間土地公廟及福德古井，據說是1983年間在澎湖當兵的士兵，在裡正角岸邊撿到漂流的神像，便搭了座簡易的佛堂讓土地公神像遮風避雨，而後在1985年由龍門村的村民林正男發起興建福德廟，供奉此尊福德正神金身。廟外的古井雖然命為福德古井，但據說比福德廟的歷史更為悠久，可惜年代久遠且沒有正確的文獻記載，已不可考。<br><br>往更裡面走，岸邊的平原處是一片廣闊的草原地，草地上可看見四尊動物造型的石雕，這些石雕都是座椅可供遊客在此稍作歇息，聽風觀浪享受片刻的寧靜。仔細觀看會發現石雕動物都朝往同個方向天空觀看，不免讓人好奇牠們在看什麼呢?',
